import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Store } from '@ngrx/store';
import { Contexto } from '../../../../shared/models/datos.models';
import { RecuperarPassRequest } from '../../../../shared/models/request.models';
import * as ContextoActions from '../../../../core/store/actions/contexto.actions';

export interface DialogData {
  usuario: string;
}

@Component({
  selector: 'app-recuperarpass-dialog',
  templateUrl: './recuperar-pass-dialog.component.html',
  styleUrls: ['./recuperar-pass-dialog.component.css']
})
export class RecuperarPassDialogComponent implements OnInit {

  username = new FormControl('', [Validators.required]);

  constructor(
    public dialogRef: MatDialogRef<RecuperarPassDialogComponent>,
    private store: Store<{ contexto: Contexto }>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) { }

  
  ngOnInit() {

  }

  onCancelarClick(): void {
    this.dialogRef.close();
  }

  recuperarPass() {
    if (this.username.valid) {
      const usuario = new RecuperarPassRequest();
      usuario.usuario = this.username.value;
      this.store.dispatch(ContextoActions.getPasswordUsuario({ usuario }));
      this.dialogRef.close();
    }
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
