import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { filter } from 'rxjs/operators';
import * as FormActions from '../../../../core/store/actions/form.actions';
import * as ContextoActions from '../../../../core/store/actions/contexto.actions';
import * as CalendarActions from '../../../../core/store/actions/calendar.actions';
import * as ReservaActions from '../../../../core/store/actions/reserva.actions';
import * as ReservacionActions from '../../../../core/store/actions/reservacion.actions';
import * as ContextSelectors from '../../../../core/store/selectors/contexto.selectors';
import { Contexto, Credencial, Login, ObraSocial, Paciente, Plan, Turno, TurnoPaciente } from '../../../../shared/models/datos.models';
import { Observable, Subscription } from 'rxjs';
import { BusquedaTFRequest, LiberarTFRequest } from '../../../../shared/models/request.models';
import * as FormSelectors from '../../../../core/store/selectors/form.selectors';
import { environment } from '../../../../../environments/environment';
import { MatDialog } from '@angular/material';
import { CancelarDialogComponent } from '../../components/cancelar-dialog/cancelar-dialog.component';
import { RecuperarPassDialogComponent } from '../../components/recuperar-pass-dialog/recuperar-pass-dialog.component';
import { contextoReducer } from '../../../../core/store/reducers/contexto.reducers';
@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})

export class LoginPageComponent implements OnInit {

  @ViewChild('footer') footerElement: ElementRef;
  
  subscription: Subscription;
  subscription2: Subscription;

  username = new FormControl('', [Validators.required]);
  password = new FormControl('', [Validators.required]);
  hidePassword = true;
  
  nickname$: Observable<string>;
  pacienteSelected$: Observable<Paciente>;

  esAnonimo$: Observable<boolean>;
  pacientes$: Observable<Paciente[]>;
  displayedColumns = ['acciones','nombre','documento', 'fecha'];
  
  turnosFuturos$: Observable<TurnoPaciente[]>;
  displayedColumnsT = ['acciones','fecha', 'especialidad'];

  credenciales$: Observable<Credencial>;

  constructor(
    public dialog: MatDialog,
    private store: Store<{ contexto: Contexto }>,
    private router: Router
  ) {
    this.pacientes$ = store.select(ContextSelectors.getPacientes);
    this.nickname$ = store.select(ContextSelectors.getNickname);
    this.pacienteSelected$ = store.select(ContextSelectors.getPacienteSelected);
    this.turnosFuturos$ = store.select(ContextSelectors.getTurnosFuturos);
    this.credenciales$ = store.select(ContextSelectors.getCredencial);

    const login = new Login();
    login.username = environment.username;
    login.password = environment.password;

    this.subscription = this.store.select(ContextSelectors.getToken).pipe(
      filter(token => token === undefined)
    ).subscribe( () => {
      this.store.dispatch(ContextoActions.getToken( { login } ));
      this.store.select(ContextSelectors.getToken).pipe(
        filter(token => (token !== undefined))
      ).subscribe(
        () => {
          this.subscription.unsubscribe();
        }
      );
    });

    this.subscription2 = this.store.select(ContextSelectors.getPacientes).subscribe(pp => {
      if (pp && pp.length > 0 ) {
        this.seleccionar(pp[0]);
        this.subscription2.unsubscribe();
      }
    });

  }


  ngOnDestroy() {
    this.subscription.unsubscribe();
    this.subscription2.unsubscribe();
  }

  ngOnInit() {
    this.esAnonimo$ = this.store.select(ContextSelectors.getEsAnonimo);
  }

  clearBusqTurnos() {
    this.store.dispatch(ReservacionActions.cleanStore());    
    this.store.dispatch(ReservaActions.cleanStore());
    this.store.dispatch(CalendarActions.cleanStore());
    this.store.dispatch(FormActions.cleanStore());
  }

  logoutPaciente() {
    this.subscription2.unsubscribe();
    this.clearBusqTurnos();
    this.store.dispatch(ContextoActions.cleanStoreLogout());
  }

  onCancelar() {
    this.logoutPaciente();
    this.router.navigate(['/home']);
  }

  logoutAndRedirect() {
    this.logoutPaciente();
    this.router.navigate(['/home']);
  }

  redirectToTurnos(paciente: Paciente) {
    this.seleccionar(paciente);
    this.router.navigate(['/home/dashboard']);
  }

  onPopupPassword() {
    this.dialog.open(RecuperarPassDialogComponent, { data: { undefined }})        
    .afterClosed().subscribe( result => {
      if (result) {
        // msj
      } else {
      }
    });
  }

  onSubmit() {
    const credencialUsuario = new Credencial();
    credencialUsuario.idUsuario = this.username.value;
    credencialUsuario.passUsurio = this.password.value;

    this.store.dispatch(ContextoActions.setCredencialUsuario({ credencialUsuario: credencialUsuario }));
    this.store.dispatch(ContextoActions.getUsuario({ credencialUsuario }));

  }
   
  
  seleccionar(paciente: Paciente) {
    this.store.select(ContextSelectors.getCredencial).subscribe((c) => {
      if (c != undefined) {
        const req: BusquedaTFRequest = {
          usuario: c.idUsuario,
          password: c.passUsurio,
          codigoPaciente: paciente.codigo
        }

        this.store.dispatch(ContextoActions.setPacienteSelected( { paciente: paciente, req: req } ));
        this.store.dispatch(FormActions.setFechaNacimiento( { fechaNacimiento: paciente.fechaNacimiento} ));

        this.store.select(FormSelectors.selectObraSocialPaciente).subscribe(
          (os: ObraSocial) => {
            this.store.dispatch(FormActions.setObraSocialSelected( { obraSocialSelected: os } ));
            this.store.select(FormSelectors.selectPlanPaciente).subscribe(
              (plan: Plan) => {
                this.store.dispatch(FormActions.setPlanSelected( { planSelected: plan} ));
              })
          }
        ).unsubscribe();
      }
    }).unsubscribe();
  }

  cancelarTurno(turno: TurnoPaciente) {

      this.dialog.open(CancelarDialogComponent, { data: { turno }})
        .afterClosed().subscribe( result => {
          if (result) {
            this.store.select(ContextSelectors.getCredencial).subscribe((c) => {
              if (c != undefined) {
                var req: LiberarTFRequest = req = {
                  usuario: c.idUsuario,
                  password: c.passUsurio,
                  codigoPaciente: undefined,
                  codigoTurno: undefined,
                  codigoTurnoEstudio: undefined
                };

                if (turno.profesionalEspecialidad != undefined && turno.profesionalEspecialidad.codigo != undefined) {
                  req.codigoTurno = turno.codigo;
                } else if (turno.servicio != undefined && turno.servicio.codigo != undefined) {
                  req.codigoTurnoEstudio = turno.codigo;
                }

                this.store.select(ContextSelectors.getPacienteSelected).subscribe((p) => {
                  if (p != undefined) {
                    req.codigoPaciente = p.codigo;
                  }
        
                  this.store.dispatch(ContextoActions.setTurnoLiberar( { req: req } ));
                }).unsubscribe();
              }
            }).unsubscribe();
          } else {
          }
        });

  } 
  
}
