import { createReducer, on } from '@ngrx/store';
import { CentroAtencion, Especialidad, Estudio, Formulario, ObraSocial, Plan, Profesional, Servicio } from '../../../shared/models/datos.models';
import * as FormActions from '../actions/form.actions';


export const initialState: 
Formulario = {
    obrasSociales: [],
    servicios: [],
    especialidades: [],
    profesionales: [],
    centrosDeAtencion: [],
    fechaNacimiento: undefined,
    obraSocialSelected: undefined,
    planSelected: undefined,
    especialidadSelected: undefined,
    profesionalSelected: undefined,
    centroDeAtencionSelected: undefined,
    servicioSelected: undefined,
    estudioSelected: undefined,
};

const _setObraSociales = (state: Formulario, obraSociales: ObraSocial[]): Formulario => {
    const stateNew = { ...state };
    stateNew.obrasSociales = obraSociales;
    return stateNew;
};

const _setObraSocialSelected = (state: Formulario, obraSocialSelected: ObraSocial): Formulario => {
    const stateNew = { ...state };
    stateNew.obraSocialSelected = obraSocialSelected;
    stateNew.planSelected = undefined;
    return stateNew;
};

const _setPlanSelected = (state: Formulario, planSelected: Plan): Formulario => {
    const stateNew = { ...state };
    stateNew.planSelected = planSelected;
    return stateNew;
};

const _setFechaNacimiento = (state: Formulario, fechaNacimiento: Date) => {
    const stateNew = { ...state };
    stateNew.fechaNacimiento = fechaNacimiento;
    return stateNew;
};

const _setProfesionales = (state: Formulario, profesionales: Profesional[]) => {
    const stateNew = { ...state };
    stateNew.profesionales = profesionales;

    
    if (state.profesionalSelected != undefined && 
        state.profesionalSelected.codigoProfesion != undefined) {

        var limpiar = true;
        profesionales.forEach(pp => {
          if (pp.codigoProfesion == state.profesionalSelected.codigoProfesion &&
            pp.matriculaProfesional == state.profesionalSelected.matriculaProfesional) {
                limpiar = false;
          }
        }); 
        if (limpiar) {
            stateNew.profesionalSelected = undefined;
        }
    }
    return stateNew;
};

const _setEspecialidades = (state: Formulario, especialidades: Especialidad[]) => {
    const stateNew = { ...state };
    stateNew.especialidades = especialidades;
    return stateNew;
};

const _setCentrosDeAtencion = (state: Formulario, centrosDeAtencion: CentroAtencion[]) => {
    const stateNew = { ...state };
    stateNew.centrosDeAtencion = centrosDeAtencion;
    return stateNew;
};

const _setServicios = (state: Formulario, servicios: Servicio[]) => {
    const stateNew = { ...state };
    stateNew.servicios = servicios;
    return stateNew;
};

const _setEspecialidadSelected = (state: Formulario, especialidadSelected: Especialidad) => {
    const stateNew = { ...state };
    stateNew.especialidadSelected = especialidadSelected;
    return stateNew;
};

const _setProfesionalSelected = (state: Formulario, profesionalSelected: Profesional) => {
    const stateNew = { ...state };
    stateNew.profesionalSelected = profesionalSelected;
    return stateNew;
};

const _setCentroDeAtencionSelected = (state: Formulario, centroDeAtencionSelected: CentroAtencion) => {
    const stateNew = { ...state };
    stateNew.centroDeAtencionSelected = centroDeAtencionSelected;
    return stateNew;
};

const _setServicioSelected = (state: Formulario, servicioSelected: Servicio) => {
    const stateNew = { ...state };
    stateNew.servicioSelected = servicioSelected;
    return stateNew;
};

const _setEstudioSelected = (state: Formulario, estudioSelected: Estudio) => {
    const stateNew = { ...state };
    stateNew.estudioSelected = estudioSelected;
    return stateNew;
};

const _cleanParcial = (state: Formulario): Formulario  => {
    const stateNew = { ...state };
    stateNew.obraSocialSelected = undefined;
    stateNew.planSelected = undefined;
    stateNew.fechaNacimiento = undefined;
    stateNew.profesionalSelected = undefined;
    stateNew.servicioSelected = undefined;
    stateNew.estudioSelected = undefined;
    stateNew.especialidadSelected = undefined;
    stateNew.centroDeAtencionSelected = undefined;
    return stateNew;
}


const _formReducer = createReducer(initialState,

    on(FormActions.cleanStore, () => initialState),

    on(FormActions.cleanStoreParcial, (state) => 
         _cleanParcial(state)),

    on(FormActions.setObraSociales, (state, { obrasSociales }) =>
        _setObraSociales(state, obrasSociales)),

    on(FormActions.setEspecialidades, (state, { especialidades }) =>
        _setEspecialidades(state, especialidades)),

    on(FormActions.setProfesionales, (state, { profesionales }) =>
    _setProfesionales(state, profesionales)),

    on(FormActions.setServicios, (state, { servicios }) =>
        _setServicios(state, servicios)),

    on(FormActions.setCentrosDeAtencion, (state, { centrosDeAtencion }) =>
        _setCentrosDeAtencion(state, centrosDeAtencion)),

    on(FormActions.setFechaNacimiento, (state, { fechaNacimiento }) =>
        _setFechaNacimiento(state, fechaNacimiento)),

    on(FormActions.setObraSocialSelected, (state, { obraSocialSelected }) =>
        _setObraSocialSelected(state, obraSocialSelected)),

    on(FormActions.setPlanSelected, (state, { planSelected }) =>
        _setPlanSelected(state, planSelected)),

    on(FormActions.setEspecialidadSelected, (state, { especialidadSelected }) =>
        _setEspecialidadSelected(state, especialidadSelected)),

    on(FormActions.setProfesionalSelected, (state, { profesionalSelected }) =>
    _setProfesionalSelected(state, profesionalSelected)),

    on(FormActions.setCentroDeAtencionSelected, (state, { centroDeAtencionSelected }) =>
        _setCentroDeAtencionSelected(state, centroDeAtencionSelected)),

    on(FormActions.setServicioSelected, (state, { servicioSelected }) =>
        _setServicioSelected(state, servicioSelected)),       

    on(FormActions.setEstudioSelected, (state, { estudioSelected }) =>
        _setEstudioSelected(state, estudioSelected)),       
);

export function formReducer(state, action) {
    return _formReducer(state, action);
}
