import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ReservaFormulario } from '../../../shared/models/datos.models';
import { ReservaTurnoEstudioRequest, ReservaTurnoRequest } from '../../../shared/models/request.models';

export const selectFormulario = createFeatureSelector<ReservaFormulario>('reserva');

export const reservarTurno = createSelector(
    selectFormulario,
    (filter: ReservaFormulario) => {
        if (!filter || !filter.turnoSelected) { return; }
        const request = new ReservaTurnoRequest();
        request.codigoTurno = filter.turnoSelected.codigo;
        // anonimo
        request.paciente = filter.paciente;
        // logueado (user y pass lo agrega en el component)
        request.codigoPaciente = filter.codigoPaciente;
        request.codigoObraSocial = filter.paciente.codigoObraSocial;
        request.codigoEspecialidad = filter.paciente.codigoEspecialidad;
        return request;
    }
);

export const reservarTurnoEstudio = createSelector(
    selectFormulario,
    (filter: ReservaFormulario) => {
        if (!filter || !filter.paciente || !filter.paciente.codigoEstudio) { return; }
        const request = new ReservaTurnoEstudioRequest();
        // anonimo
        request.paciente = filter.paciente;
        // logueado (user y pass lo agrega en el component)
        request.codigoPaciente = filter.codigoPaciente;
        request.codigoObraSocial = filter.paciente.codigoObraSocial;
        request.codigoEspecialidad = filter.paciente.codigoEspecialidad;
        request.codigoServicio = filter.paciente.codigoServicio;
        request.codigoEstudio = filter.paciente.codigoEstudio;
        request.diaSugerido = filter.paciente.diaSugerido;
        request.credencial = filter.paciente.credencial;
        request.pedidoMedico = filter.paciente.pedidoMedico;
        request.varios = filter.paciente.varios;
        return request;
    }
);

export const getReserva =  createSelector(
    selectFormulario,
    (reservaSelected: ReservaFormulario) => {
        return reservaSelected;
    }
);

export const getTurnoSelected = createSelector(
    selectFormulario,
    (reserva: ReservaFormulario) => reserva.turnoSelected
);

