const diasDisponibles = () => {
    const response: DisponibilidadDias[] = [];
    DateUtils.getDaysArray(new Date(), 9).forEach(
        (day: Date, index: number) => {
            response.push({
                fecha: '2022-07-0' + index,
                conDisponibilidadTM: index % 2 === 0,
                conDisponibilidadTT: index % 3 === 0
            });
        }
    );
    return response;
};

