import { createSelector, createFeatureSelector } from '@ngrx/store';
import { Formulario, ObraSocial, Profesional, Especialidad, Contexto, Plan, Servicio } from '../../../shared/models/datos.models';
import { BusquedaProfesionalesRequest, DatosFormulario, BusquedaRequest } from '../../../shared/models/request.models';

export const selectFormulario = createFeatureSelector<Formulario>('formulario');
export const selectContexto = createFeatureSelector<Contexto>('contexto');

export const selectAllObrasSociales = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.obrasSociales
);

export const selectAllServicios = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.servicios
);

export const selectAllProfesionales = createSelector(
    selectFormulario,
    (formulario: Formulario) => {
        if (formulario.especialidadSelected != undefined && formulario.especialidadSelected.codigo != undefined) {
            return formulario.profesionales.filter(x => 
                x.especialidad.filter(esp => esp.codigo === formulario.especialidadSelected.codigo).length>0
            );
        }
        return formulario.profesionales;
    }
);

export const selectAllEspecialidades = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.especialidades
);

export const selectAllCentrosDeAtencion = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.centrosDeAtencion
);




export const selectCentroAtencionSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => { 
        return formulario.centroDeAtencionSelected 
    }
);

export const selectObraSocialSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => formulario.obraSocialSelected
);

export const selectServicioSelected = createSelector(
    selectFormulario,
    (formulario: Formulario) => {
        return formulario.servicioSelected
    }
);

export const selectCentrosDeAtencion = createSelector(
    [selectServicioSelected, selectFormulario],
    (servicio: Servicio, formulario: Formulario) => {
        if (servicio) {
            return servicio.centroAtencion;
        }
        return formulario.centrosDeAtencion          
    }
);

export const selectCodigoObraSocialPaciente = createSelector(
    selectContexto,
    (contexto: Contexto) => {
        if (contexto.pacienteSelected != undefined)  {
            return contexto.pacienteSelected.codigoObraSocial
        } else {
            return undefined;
        }
    }
);
export const selectCodigoPlanPaciente = createSelector(
    selectContexto,
    (contexto: Contexto) => {
        if (contexto.pacienteSelected != undefined)  {
            return contexto.pacienteSelected.codigoPlan
        } else {
            return undefined;
        }
    }
);

export const selectObraSocialPaciente = createSelector(
    [selectCodigoObraSocialPaciente, selectAllObrasSociales],
    (codigoObraSocial: number, allOS: ObraSocial[]) => {
        var list = allOS.filter(x => x.codigo === codigoObraSocial);
        if (list && list.length == 1) {
            return list[0];
        } else {
            return undefined;
        }
    }
);

